
  .home-wrapper{
    padding: 3em;
    margin: 0 auto;
    text-align: center;
  }

  .info-container {
    margin: 0 auto;
    width: 70%; 
  }

  .name{
        font-family: fantasy;
        font-size: 4rem;
  }

  .title{
    font-family: fantasy;
        font-size: 2rem;
  }
  .about{
    font-family: fantasy;
    font-size: 1.3rem;
    margin-top: 2rem;
    margin-bottom: 2rem;
  }

  .social-container{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    -ms-flex-align: center;
        align-items: center;
  }

  a{
     color: black;
     text-decoration: none;
  }


  @media all and (max-width: 430px){
    .social-container{
        -ms-flex-flow: column wrap;
            flex-flow: column wrap;
    }
    .social-container a{
        padding-top: 2em;
    }
    
    
}



 

   
  
  


