.list-item {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-align: center;
        align-items: center;
    width: 45%;
    height: auto;
    margin: 1em auto;
    padding: 0;
    text-align: center; 
    border-radius: 10px;
    -webkit-box-shadow: 2px 2px 10px 0 rgba(0,0,0,.3);
            box-shadow: 2px 2px 10px 0 rgba(0,0,0,.3);
}
 
.list-item:hover .list-content {
    opacity: 0.80; 
}


.list-image {
  -o-object-fit: fill;
     object-fit: fill;
  width: 100%;
  height: auto;
  overflow: hidden;
  border-radius: 10px;
}

      
.list-content {
    background-color: black;
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
    height: 100%;
    padding: 1rem;
    color: #FAF0E6;
    font-size: 15px;
    opacity: 0;
    border-radius: 10px;
    -webkit-transition: opacity 0.25s ease-in 0.1s;
    -o-transition: opacity 0.25s ease-in 0.1s;
    transition: opacity 0.25s ease-in 0.1s; 
}


.list-links-container{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row nowrap;
        flex-flow: row nowrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    width: 30%;
    margin: 1rem;
    padding: 0.5rem;
}

.list-links-container a {
    color: #00BFFF;
    font-size: 1.2rem;
}

.list-links-container a:hover {
    color: #FAF0E6;
    border-bottom: 1px solid white;
   
}

  
@media all and (max-width: 900px){
    .list-item{
        width: 80%; 
        -ms-flex-positive: 1; 
            flex-grow: 1;
        height: auto;
        -ms-flex-preferred-size: auto;
            flex-basis: auto;
    }
    
}


@media all and (max-width: 600px){
    .list-item{
        width: 100%;
    }

    .list-links-container{
         width: 30%;
    }
    
}



